@font-face {
  font-family: "allura";
  src: url(./font/Allura-Regular.ttf);
}
@font-face {
  font-family: "roboto";
  src: url(./font/Roboto-Light.ttf);
}

body {
  background: linear-gradient(rgb(29, 29, 29), rgb(26, 26, 26));
}
header {
  background: rgb(49, 45, 41);
  border-radius: 20px;
  padding: 10px;
  width: 80%;
  margin: 20px auto;
  text-align: center;
}
header h1 {
  width: 62.5%;
  margin: 5px auto;
  font-family: "allura", serif;
  letter-spacing: 4px;
  font-size: 3rem;
  color: rgb(211, 118, 11);
}
header p {
  text-align: left;
  color: rgb(209, 190, 169);
  padding: 10px;
  margin: 20px;
  font-family: "roboto", serif;
  line-height: 1.5rem;
  letter-spacing: 1px;
}
.main p.select {
  font-style: italic;
  color: rgb(211, 118, 11);
  font-family: "allura", serif;
  letter-spacing: 2px;
  font-size: 1.5rem;
  text-align: center;
}

.ul-select {
  width: 50%;
  background: rgb(49, 45, 41);
  border-radius: 20px;
  padding: 10px;
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.ul-select li {
  cursor: pointer;
}
.select-html-css:hover {
  background: rgb(211, 118, 11);
}
.select-js:hover {
  background: rgb(211, 118, 11);
}
.select-react:hover {
  background: rgb(211, 118, 11);
}
.overview h3 {
  text-align: left;
  color: rgb(209, 190, 169);
  padding: 10px;
  margin: 20px;
  font-family: "roboto", serif;
  line-height: 2.5rem;
  letter-spacing: 1px;
  text-align: center;
}
.block-category {
  border: 1px solid rgb(49, 45, 41);
  margin: 20px;
  border-radius: 50px;
  transition: 1s;
  display: none;
}
.category {
  display: flex;
  flex-wrap: wrap;
}

.category li {
  position: relative;
  width: 300px;
  background: rgb(49, 45, 41);
  border-radius: 15px;
  margin: 20px;
  padding: 10px;
  text-align: center;
}
.category li span.star-vide {
  /* display: none; */
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 2rem;
  cursor: pointer;
}
.category li span.star-vide:hover {
  color: rgb(255, 145, 0);
}
.category li span.star-pleine {
  /* display: none; */
  position: absolute;
  top: 10px;
  right: 10px;
  color: rgb(255, 145, 0);
  font-size: 2rem;
  cursor: pointer;
}

.category li h3 {
  margin: 0x auto;
  font-family: "allura", serif;
  letter-spacing: 2px;
  font-size: 2rem;
  color: rgb(211, 118, 11);
}

.category li img {
  width: 250px;
  border-radius: 25px;
  opacity: 0.7;
  border: solid 1px rgb(19, 16, 14);
  transition: 1s;
  cursor: pointer;
}
.category li img:hover {
  transform: scale(1.75);
  opacity: 1;
}
.category li p {
  text-align: left;
  color: rgb(209, 190, 169);
  padding: 10px;
  margin: 20px;
  font-family: "roboto", serif;
  line-height: 1.5rem;
  letter-spacing: 1px;
}
.second-div-select {
  display: none;
}
